import React, { useEffect, useRef, useState } from 'react';
import { Progress } from 'antd';
import 'ol/ol.css';
import { Map, View } from 'ol';
import TileLayer from 'ol/layer/Tile';
import OSM from 'ol/source/OSM';
import Style from 'ol/style/Style';
import Icon from 'ol/style/Icon';
import Feature from 'ol/Feature';
import { fromLonLat } from 'ol/proj'
import Point from 'ol/geom/Point';
import VectorLayer from 'ol/layer/Vector';
import VectorSource from 'ol/source/Vector';
import { useNavigate } from 'react-router-dom';
import Warning from '@/Components/svg/Warning';
import { byElder, alertHistory, allElderProfile, allElderlyList, indexLati } from '@/api/RecentAlerts'
import { getElders } from '@/api/detailOfElderly'
import TabMenus from '@/Components/TabMenus'
import Avatar from '@/Components/svg/Avatar'
import Bed from '@/Components/svg/bed'
import ArrowRight from '@/Components/svg/ArrowRight'
import ArrowLeft from '@/Components/svg/ArrowLeft'
import Timer from '@/Components/svg/Timer'
import RightPage from './components/RightPage'
import dayjs from 'dayjs';
import RoomView from './components/RoomView'
import './index.scss'
import redIcon from '@/assets/icon/home/red-icon.svg'
import redIconClick from '@/assets/icon/home/red-click.svg'

import blueIcon from '@/assets/icon/home/blue-icon.svg'
import blueconClick from '@/assets/icon/home/blue-click.svg'

import yellowIcon from '@/assets/icon/home/yellow-icon.svg'
import yellowConClick from '@/assets/icon/home/yellow-click.svg'

import { useCountStore } from '@/store/index';
import MarkPhone from '@/Components/MarkPhone'
import { formatDuration } from '@/utils/seenYouUtil'
import sidebarlogo from "@/assets/sidebarlogo.svg";

import axios from 'axios';
import ls from 'store2';
import gcoord from 'gcoord';

const RecentAlerts = () => {
    const navigate = useNavigate()
    const user = useCountStore(state => state.user);
    const mapRef = useRef();
    const layerRef = useRef(); // 使用 useRef 来存储 layer 的引用
    const [menus, setMenus] = useState([
        {
            text: 'All',
            bo: true,
            warring: false
        },
        {
            text: 'Critical',
            bo: false,
            warring: false
        },
        {
            text: 'Warnings',
            bo: false,
            warring: false
        },

        // {
        //     text: 'Low Priority',
        //     bo: false,
        //     warring: false
        // }
    ])



    const [elmenus, setElMenus] = useState([
        {
            text: 'Map View',
            bo: true,
            warring: false
        },
        {
            text: 'Room View',
            bo: false,
            warring: false
        },

    ])


    const [eloading, setEloading] = useState(false)
    const [elderLists, setElderLists] = useState([])
    const [activeTab, setActiveTab] = useState('All')
    const [activeTab1, setActiveTab1] = useState('Map View')

    const [targetmenus, setTargetMenus] = useState([[]])

    const [query, setQuery] = useState({
        "elderIds": undefined,
        "alarmType": -1
    })
    const [current, setCurrent] = useState(0)
    let map = null; //地图全局变量
    const minutesAgo = (time) => {
        const now = dayjs();
        const then = dayjs(time);
        const diffInMinutes = now.diff(then, 'minute');
        const diffInHours = Math.floor(diffInMinutes / 60);
        const remainingMinutes = diffInMinutes % 60;

        if (diffInHours >= 1) {
            return `${diffInHours} Hours ${remainingMinutes} Minutes ago`;
        } else {
            return `${diffInMinutes} Minutes ago`;
        }
    }
    const sliceArrayIntoGroups = (arr, groupSize) => {
        if (arr.length - 1 < groupSize) return [arr]
        // 创建一个空数组，用于存储最终的分组结果
        let result = [];
        // 使用 for 循环遍历原数组，步长为 groupSize（在本例中为4）
        for (let i = 0; i < arr.length; i += groupSize) {
            // 使用 slice 方法从原数组中提取当前位置开始的 groupSize 个元素
            // 并将这个子数组添加到结果数组中
            result.push(arr.slice(i, i + groupSize));
        }
        // 返回分组后的结果数组
        return result;
    }
    const handleArrSlice = (text) => {
        if (text == "right") {
            if (current <= targetmenus.length - 1) {
                setCurrent(current + 1)
            }
        } else {
            if (current >= 1) {
                setCurrent(current - 1)
            }
        }


    }

    function timeDifference(targetTime) {
        const now = new Date();
        const targetDate = new Date(targetTime);
    
        let diff = Math.abs(targetDate - now); // 差异时间的绝对值，单位为毫秒
    
        // 计算各个时间单位
        const days = Math.floor(diff / (1000 * 60 * 60 * 24));
        diff -= days * (1000 * 60 * 60 * 24);
        const hours = Math.floor(diff / (1000 * 60 * 60));
        diff -= hours * (1000 * 60 * 60);
        const minutes = Math.floor(diff / (1000 * 60));
        diff -= minutes * (1000 * 60);
    
        // 生成结果字符串
        let result = "";
        if (days > 0) result += `${days} Day${days > 1 ? 's' : ''} `;
        if (hours > 0 || days > 0) result += `${hours} Hour${hours > 1 ? 's' : ''} `;
        result += `${minutes} Minute${minutes > 1 ? 's' : ''}`;
    
        return result.trim();
    }
    
    const getCardList = () => {
        if (query.alarmType == -1) return


        let priority = query.alarmType ? query.alarmType : ""
        setEloading(true)
        let params = {
            "elderId": null,
            "startTime": null,
            "endTime": dayjs().format('YYYY-MM-DD'),
            "testDemo":0,
            "priority": priority,
            "type": 0
          }
          setTargetMenus([[]])
        // byElder(priority)
        alertHistory(params)
            .then(({ data }) => {
                console.log(data);
                setEloading(false)
                if (data.length == 0) {
                    setTargetMenus([[]])
                    return
                }
                console.log(data);
                
                const nowTime = new Date().getTime()
                data = data.map((e, index) => {
                    const alertTime = new Date(e.alarmStartTime).getTime()
                    const rangeTime = nowTime - alertTime
                    console.log(rangeTime)
                    console.log(60*1000*15)
                    if(rangeTime < 60*1000*15) {
                        e.percent = 5
                    } else if(rangeTime >= 60*1000*15 && rangeTime < 60*1000*30) {
                        e.percent = 25
                    } else if(rangeTime >= 60*1000*30 && rangeTime < 60*1000*45) {
                        e.percent = 50
                    } else if(rangeTime >= 60*1000*45 && rangeTime < 60*1000*60) {
                        e.percent = 75
                    } else {
                        e.percent = 100
                    }
                    console.log(e)
                    return e
                })
                let dataarray = []
                // data.push(data[0], data[0], data[0], data[0])
                console.log(data)
                data = data.filter(res=>res.type !== 1)
                dataarray = sliceArrayIntoGroups(data, 5)
                setTargetMenus(dataarray)

            })

    }
    const handleNavto = (row) => {
        console.log("row", row)
        navigate(`/Agentadmin/dashboard/Support-Agents/${row.elderId}`)
    }
    // 动态添加标记点的函数
    function addMarker(longitude, latitude) {


        const point = new Point(fromLonLat([longitude, latitude])); // 创建点几何对象，转换坐标系
        const pointFeature = new Feature(point); // 创建特征
        // 设置特征的样式，包括图标
        const iconStyle = new Style({
            image: new Icon({
                anchor: [0.5, 46], // 图标的锚点，这里的意思是图标的中心位于特征点的正下方
                anchorXUnits: 'fraction',
                anchorYUnits: 'pixels',
                src: yellowIcon //redIcon // 图标的URL
            })
        });
        pointFeature.setStyle(iconStyle); // 将样式应用到特征上
        layerRef.current.getSource().addFeature(pointFeature); // 将特征添加到矢量数据源中
    }
    const mapInit = async () => {
        let coordinates = []
        // const latis = ls.session.get('mapviewLati') || []
        // const lists = ls.get('elderLists')
        // if (false && latis.length) { // 不用缓存了, 会引起数据错误
        //     coordinates = latis
        //     setElderLists(lists)
        // } else {
            
        // }

        let elderData =  await allElderlyList() //await selfList({})
        let datas = elderData.data || []
        console.log(elderData);
        // datas = datas.filter(res=>res.priority > 0)
        setElderLists(datas)
        
        ls.set('elderLists',datas)
        
        for (const item of datas) {
            const loc = item.address
            try {
                if (loc) {
                    // const response = await axios.get('https://nominatim.openstreetmap.org/search', {
                    //     params: {
                    //       q: loc,
                    //       format: 'json',
                    //       addressdetails: 1,
                    //     },
                    //   });
                    // // if (!response.ok) {
                    // //     throw new Error(`Error: ${response.status}`);
                    // // }
                    // const { lat, lon } = response.data[0];
                    // coordinates.push({
                    //     longitude: lon,
                    //     latitude: lat
                    // })

                    const apiKey = 'AIzaSyB9VY3CN5EzIDwWzMd08zZCS6wlmroZ5Ns';
                    const response = await axios.get('https://maps.googleapis.com/maps/api/geocode/json', {
                        params: {
                            address: loc,
                            key: apiKey,
                        },
                    });
                    
                    if (response.data.results.length > 0) {
                        const { lat, lng } = response.data.results[0].geometry.location;
                        coordinates.push({
                            longitude: lng,
                            latitude: lat
                        })
                    } else {
                        console.log('No results found');
                    }
                }
            } catch (error) {
                console.error(`Failed to fetch data from ${loc}:`, error);
            }
        }
        coordinates = coordinates.map(e => {
            return [e.longitude, e.latitude]
        })
        console.log(coordinates);
        
        ls.session.set('mapviewLati', coordinates)

        if (!map) {
            map = new Map({
                target: mapRef.current,
                layers: [
                    new TileLayer({
                        source: new OSM()

                    })
                ],
                view: new View({
                    center: fromLonLat([-110.0361, 38.5891]),
                    zoom: 1
                }),

            });
            const features = coordinates.map(coord => {
                const feature = new Feature(new Point(fromLonLat(coord)));
                feature.setStyle(new Style({
                    image: new Icon({
                        anchor: [0.5, 0.5],
                        anchorXUnits: 'fraction',
                        anchorYUnits: 'fraction',
                        src: yellowIcon //redIcon
                    })
                }));
                return feature;
            });
            const layer = new VectorLayer({
                source: new VectorSource({
                    features: features
                })
            });

            layerRef.current = layer;

            map.addLayer(layer);

            let useLargeIcon = false;
            setInterval(() => {
                useLargeIcon = !useLargeIcon;
                // const newIconSrc = useLargeIcon ? redIconClick : redIcon;

                features.forEach((feature, index) => {
                    // console.log('index',index)
                    let row = datas[index] || {}
                    // console.log(datas)
                    // console.log(row)
                    let icon = ""
                    if (row.priority == 1) {
                        if (useLargeIcon) {
                            icon = yellowIcon
                        } else {
                            icon = yellowConClick
                        }
                    } else if (row.priority == 2) {
                        if (useLargeIcon) {
                            icon = redIcon
                        } else {
                            icon = redIconClick
                        }
                    } else {
                        if (useLargeIcon) {
                            icon = blueconClick
                        } else {
                            icon = blueIcon
                        }
                    }
                    const newStyle = new Style({
                        image: new Icon({
                            anchor: [0.5, 0.5],
                            anchorXUnits: 'fraction',
                            anchorYUnits: 'fraction',
                            src: icon // 直接使用新的图标源
                        })
                    });
                    feature.setStyle(newStyle); // 为特征设置新的样式
                });

                // 刷新图层来应用新的样式

            }, 2000); // 每2秒切换一次图标
        }
    }
    useEffect(() => {
        mapInit()
        // return () => {
        //     ls.session.set('mapviewLati',[])
        // }
    }, [activeTab1])
    useEffect(() => {
        setQuery({ ...query, alarmType: activeTab === 'All' ? null : activeTab === 'Critical' ? 2 : activeTab === 'Warnings' ? 1 : null })
        console.log(targetmenus);
        
    }, [activeTab])
    useEffect(() => {
        getCardList()
    }, [query])

    const intervalRefRec = useRef(null);
    useEffect(() => {
        // 当组件挂载或更新时启动定时器
        const startTimer = () => {
          intervalRefRec.current = setInterval(() => {
            mapInit()
          }, 30*1000); // 每隔1秒执行一次
        };
    
        // 启动定时器
        startTimer();
    
        // 当组件卸载时清除定时器
        return () => {
          if (intervalRefRec.current) {
            clearInterval(intervalRefRec.current);
            console.log('RecTimer stopped');
          }
        };
    }, []); // 空数组意味着只在组件挂载和卸载时运行

    const curColor = (type) => {
        return type === 2 ? '#eb3d3d' : '#FAB515'
    }

    return (
        <div className="flex h-full">
            <div className="flex-grow bg-white">
                <div className='flex h-[100px] justify-between  items-center mr-[50px] '>
                    <div className='text-[28px] font-semibold' >Recent Alerts</div>
                    <MarkPhone />
                    {/* <BlockButton>
                        <img src={mobil} />
                        <div className='pl-[20px]  text-white text-[14px] font-semibold  '>Call 911</div>
                    </BlockButton> */}
                </div>
                <div className='flex pt-[20px] pb-[10px]'>
                    <TabMenus menus={menus} activeTab={activeTab} setActiveTab={setActiveTab} />
                </div>
                {
                    // targetmenus[current]?.length && !eloading ? null:
                    eloading ? 
                    <div className='flex pt-[20px] pb-[20px] items-center relative' style={{height:'200px',color:'#bbb'}}>
                        <div style={{width:'100%',textAlign:'center'}}>Alerts are loading...</div>
                    </div> : 
                    targetmenus[current]?.length === 0 ? 
                    <div className='flex pt-[20px] pb-[20px] items-center relative' style={{height:'200px',color:'#bbb'}}>
                        <div style={{width:'100%',textAlign:'center'}}>All Good</div>
                    </div>: null
                }
                
                <div className='flex pt-[20px] pb-[20px] items-center  relative' >

                    {
                        current >= 1 && (<div onClick={() => handleArrSlice("left")}
                            className='h-full absolute flex items-center  left-[-40px] cursor-pointer '>
                            <ArrowLeft />
                        </div>)
                    }
                    <div className='grid grid-cols-5 gap-[20px] pr-[40px] w-full'>
                        {

                            targetmenus[current]?.map((item, index) => {

                                return (
                                    <div onClick={() => handleNavto(item)} key={index}
                                        style={{ boxShadow: ' 0px 0px 50px -10px rgba(0, 0, 0, 0.15)', backgroundColor: item.back }}
                                        className={`card
                                        h-[200px]
                                rounded-[20px]
                                 pl-[20px] 
                                 pr-[20px] text-OnButtonNormal  
                                 rounded-[20px] 
                                  cursor-pointer  
                                  flex 
                                  flex-col
                                  justify-between
                                  pt-[20px]
                                  pb-[20px]
                                  items-center  ${item.alarmType == 0 ? 'bg-onBackWarring  ' : 'bg-white'} `}>
                                        <div className='flex  flex-col items-center card-title w-full '>
                                            <div
                                                style={{ backgroundColor: curColor(item.priority) }}
                                                className={`
                                           flex justify-center items-center
                                         rounded-[100px]
                                         pl-[10px]
                                         pr-[10px]
                                         pt-[11px]

                                         pb-[11px]
                                        
                                        ${item.alarmType == 0 ? 'bg-white' : item.alarmType == 1 ? ' bg-Warning' : "bg-onBackWarring"}
                                          `}>
                                                <Warning color={'#fff'} />
                                            </div>
                                            <div
                                                style={{ color: "#333" }}
                                                className={`mt-[10px]  card-name
                                                2xl:text-[16px]
                                                xl:text-[14px]
                                         text-center font-cblock text-base font-semibold leading-normal
                                        }
                                        `}>
                                                {item.alertName}
                                            </div>
                                        </div>
                                        <div className='w-full    statuslist mt-[20px] '>
                                            <div className='w-full flex  items-center '>
                                                <Avatar color={'#444'} />
                                                <div style={{ color: '#444' }} className='w-full line-1 ml-[5px] text-cblock text-[12px] font-semibold'>
                                                    {item.lastName || '--'}
                                                    {/* ({item.alertCount}) */}
                                                </div>
                                            </div>
                                            <div className='w-full flex  items-center mt-[5px] '>
                                                <Bed color={'#444'} />
                                                <div style={{ color: '#444' }} className='w-full line-1 ml-[5px] text-cblock text-[12px] '>Room {item.roomNumber}</div>
                                            </div>
                                            <div className='w-full flex  items-center  mt-[5px] '>
                                                <Timer color={'#444'} />
                                                <div style={{ color: '#444' }} className='w-full line-1 ml-[5px] text-cblock text-[12px]'>{item.alarmStartTime}</div>
                                            </div>
                                        </div>
                                        <div className='xl:w-[100px] sm:w-[60px] press'>
                                            <Progress
                                                showInfo={false} strokeColor={curColor(item.priority)} percent={item.percent} />
                                        </div>
                                        <div style={{ color: item.color }} className={` line1 w-full lable xl:text-[14px]  sm:text-[12px]  text-center `} >
                                            {/* {item.lable} */}{timeDifference(item.alarmStartTime)} ago
                                        </div>
                                    </div>
                                )
                            })
                        }
                    </div>
                    {
                        current < targetmenus.length - 1 && (
                            <div className='h-full absolute flex items-center right-[5px] cursor-pointer ' onClick={() => handleArrSlice("right")}>
                                <ArrowRight />
                            </div> 
                        )
                    }

                </div>
                <div className='pt-[30px] flex items-center '>
                    <div className="text-[20px] font-cblock text-lg font-semibold ">Elderlies</div>
                    <div className='ml-[20px]'>
                        <TabMenus menus={elmenus} activeTab={activeTab1} setActiveTab={setActiveTab1} />
                    </div>

                </div>

                <div className='mt-[20px] pr-[30px]'>
                    {
                        activeTab1 == "Map View" && (
                            <div className='h-[550px]  mt-[20px] ' ref={mapRef}>
                                <p style={{ position: 'relative', top: '200px', textAlign: 'center', color: '#999' }}>Loading...</p>
                            </div>
                        )
                    }
                    {
                        activeTab1 == "Room View" && (
                            <RoomView activeTab={activeTab1} elderLists={elderLists} />
                        )
                    }
                </div>
            </div>

            <div className='flex items-center' style={{position: 'absolute',top:'20px',right:'20px'}}>
                <div className='overflow-hidden cursor-pointer border border-2 border-lightwhite hover:border-cblock rounded-full  ' 
                    style={{backgroundColor:'#455575',border:'1px solid #1c3668'}}>
                    <img className='h-[53px]' src={sidebarlogo || user.iconDetail} />
                </div>
            </div>
        
            {/* <div className="sm:basis-[350px]  sm:w-[350px]  2xl:basis-[565px] 2xl:w-[565px]   lg:basis-[450px] lg:w-[450px] md:basis-[400px]  md:w-[400px] sm:basis-[350px]  sm:w-[350px] bg-[#F7F7FF]">
                <RightPage user={user} />
            </div> */}
        </div>
    );
};

export default RecentAlerts;