import React, { useEffect, useRef, useState } from 'react';
import { LeftOutlined, RightOutlined } from '@ant-design/icons';
import dayjs from 'dayjs';
import weekday from 'dayjs/plugin/weekday';
import weekOfYear from 'dayjs/plugin/weekOfYear';
import isoWeeksInYear from 'dayjs/plugin/isoWeeksInYear';
import { recentMedication,userGetById } from '@/api/RecentAlerts'
import search from '@/assets/search.svg'
import group from '@/assets/group.svg'
import user1 from '@/assets/user1.png'

import BlockButton from '@/Components/BlockButton'
import CaleInfo from './CaleInfo'
import Botifications from './Botifications'
import SerchNew from './SerchNew'
import RestInfo from './RestInfo'
import sidebarlogo from "@/assets/sidebarlogo.svg";
dayjs.extend(weekday);
dayjs.extend(weekOfYear);
dayjs.extend(isoWeeksInYear);
const RightPage = ({user}) => {
    const [date, setDate] = useState([])

    const [dataClick, setDataClick] = useState("")
    const [dataIndex, SetDataIndex] = useState(0)
    const [cards, SetCards] = useState([])
    const [tabIndex, setTabIndex] = useState(0)
    const generateMonthlyData = () => {
        // 获取当前年份和月份
        const now = dayjs();
        const year = now.year();
        const month = now.month() + 1; // dayjs 中月份是从 0 开始的，+1 使其符合常规表示

        // 获取这个月的天数
        const daysInMonth = now.daysInMonth();

        // 生成这个月的日期数据数组
        const dateData = [];
        for (let day = 1; day <= daysInMonth; day++) {
            const currentDate = dayjs(new Date(year, month - 1, day));
            dateData.push({
                text: day,
                bo: false, // 假设初始时没有日期被选中
                label: currentDate.format('ddd').charAt(0).toUpperCase(), // 英文缩写首字母大写
                value: currentDate.format('YYYY-MM-DD') // 完整日期值
            });
        }

        // 将日期数据数组分组，每组5天
        const groupSize = 7;
        const dateGroups = [];
        for (let i = 0; i < dateData.length; i += groupSize) {
            dateGroups.push(dateData.slice(i, i + groupSize));
        }

        return dateGroups;
    }
  
    const handleChange = (text) => {
        if (text == "left") {
            if (dataIndex == 0) return
            SetDataIndex(dataIndex - 1)
        } else {
            let len = date.length - 1
            if (dataIndex == len) return
            SetDataIndex(dataIndex + 1)
        }
    }
    const handleStatusChange = () => {
        // const [tabIndex, setTabIndex] = useState(2)
        if (tabIndex == 2) {
            setTabIndex(0)
        } else {
            setTabIndex(2)
        }
    }
    const handleDatePop = (row) => {

        setDataClick(row.value)
        recentMedication({ dateInfo: row.value })
            .then(data => {

                SetCards(data.data)
            })
    }
    useEffect(() => {
        setDate(generateMonthlyData())
    }, [])
    return (

        <div className=' 2xl:mr-[50px] 2xl:ml-[50px]  lg:mr-[30px] lg:ml-[30px] md:mr-[10px] md:ml-[10px] '>
            <div className='flex h-[100px] justify-between  items-center '>
                {/* <div className='h-[40px] pl-[20px] pr-[20px]  rounded-[20px] bg-cblock   flex items-center '>
                       
                    </div> */}
                <div>
                    {/* <BlockButton onClick={() => handleStatusChange()}>
                        <div className='flex'>
                            <img src={search} />
                            {
                                tabIndex == 2 && <div className='ml-[40px] text-[14px] font-[600] text-white' >Emily Robinson</div>
                            }

                        </div>
                    </BlockButton> */}
                </div>
                <div className='flex items-center '>
                    {/* <div onClick={() => setTabIndex(1)} className=' cursor-pointer relative mr-[26px] hover:brightness-75'>
                        <img src={group} />

                        <div className=' bottom-[3px]  right-[0px]  absolute w-[8px] h-[8px] bg-onBackWarring rounded-[100px]'></div>
                    </div> */}
                    <div  onClick={() => setTabIndex(3)}  className='overflow-hidden cursor-pointer border border-2 border-lightwhite hover:border-cblock rounded-full  ' 
                        style={{backgroundColor:'#455575',border:'1px solid #1c3668'}}>
                        <img className='h-[53px]' src={sidebarlogo || user.iconDetail} />
                    </div>
                </div>
            </div>

            {
                tabIndex == 0 && <CaleInfo  setTabIndex={setTabIndex} status={tabIndex} />
            }
            {
                tabIndex == 1 && <Botifications setTabIndex={setTabIndex} status={tabIndex} />
            }
            {
                tabIndex == 2 && <SerchNew setTabIndex={setTabIndex} status={tabIndex} />
            }
            {
                tabIndex==3&&<RestInfo setTabIndex={setTabIndex} status={tabIndex} />
            }
        </div>

    )
}
export default RightPage