
import React, { useEffect, useRef, useState } from 'react';
import { LeftOutlined, RightOutlined } from '@ant-design/icons';
import dayjs from 'dayjs';
import weekday from 'dayjs/plugin/weekday';
import weekOfYear from 'dayjs/plugin/weekOfYear';
import isoWeeksInYear from 'dayjs/plugin/isoWeeksInYear';
import { recentMedication } from '@/api/RecentAlerts'
import user1 from '@/assets/user1.png'
import NoEvent from './NoEvent'



dayjs.extend(weekday);
dayjs.extend(weekOfYear);
dayjs.extend(isoWeeksInYear);
export default function CaleInfo({ status }) {
    const [date, setDate] = useState([])

    const [dataClick, setDataClick] = useState("")
    const [dataIndex, SetDataIndex] = useState(0)
    const [cards, SetCards] = useState([])
    const [tabIndex, setTabIndex] = useState(0)
    const generateMonthlyData = () => {
        // 获取当前年份和月份
        const now = dayjs();
        const year = now.year();
        const month = now.month() + 1; // dayjs 中月份是从 0 开始的，+1 使其符合常规表示

        // 获取这个月的天数
        const daysInMonth = now.daysInMonth();

        // 生成这个月的日期数据数组
        const dateData = [];
        for (let day = 1; day <= daysInMonth; day++) {
            const currentDate = dayjs(new Date(year, month - 1, day));
            dateData.push({
                text: day,
                bo: false, // 假设初始时没有日期被选中
                label: currentDate.format('ddd').charAt(0).toUpperCase(), // 英文缩写首字母大写
                value: currentDate.format('YYYY-MM-DD') // 完整日期值
            });
        }

        // 将日期数据数组分组，每组5天
        const groupSize = 7;
        const dateGroups = [];
        for (let i = 0; i < dateData.length; i += groupSize) {
            dateGroups.push(dateData.slice(i, i + groupSize));
        }

        return dateGroups;
    }
    const handleChange = (text) => {
        if (text == "left") {
            if (dataIndex == 0) return
            SetDataIndex(dataIndex - 1)
        } else {
            let len = date.length - 1
            if (dataIndex == len) return
            SetDataIndex(dataIndex + 1)
        }
    }
    const handleDatePop = (row) => {

        setDataClick(row.value)
        recentMedication({ dateInfo: row.value })
            .then(data => {

                SetCards(data.data)
            })
    }
    const init = () => {
        if (status == 0) {
            setDate(generateMonthlyData())
        }
    }
    useEffect(() => {
        init()
    }, [status])
    return (
        <>
            <div>
                <div className='mt-[40px] '>
                    <div className='mt-[18.5px] flex items-center   justify-between'>
                        <div className='font-[20px]  font-cblock font-semibold' >Dec 2023</div>
                        <div className='flex'>
                            <div onClick={() => { handleChange("left") }} className='cursor-pointer w-[32px] h-[32px] bg-OnButtonNormal rounded-[100px] flex items-center justify-center mr-[5px] '> <LeftOutlined className="text-white" /></div>
                            <div onClick={() => { handleChange("right") }} className='cursor-pointer w-[32px] h-[32px] bg-OnButtonNormal rounded-[100px] flex items-center justify-center '> <RightOutlined className="text-white" /></div>
                        </div>
                    </div>
                </div>
                <div className='mt-[37.5px] flex  2xl:w-[472px] lg:w-[400px] sm:w-[300px] ' >
                    {
                        date.length > 0 && date[dataIndex]?.map((item, index) => {
                            return (
                                <div
                                    key={index}
                                    onClick={() => handleDatePop(item)}
                                    className={`${index == 6 ? "" : "mr-[20px]"} hover:border-cblock border   cursor-pointer  w-[50px] h-[80px] rounded-[13px] flex flex-col items-center justify-center ${item.value == dataClick ? "bg-[#FAB515]" : "bg-white"} `}
                                    style={{ boxShadow: "0px 0px 50px -10px rgba(0, 0, 0, 0.25)" }} >
                                    <div className={`text-[16px] font-semibold  ${item.value == dataClick ? "text-white" : "text-cblock"} `}>{item.text}</div>
                                    <div className={`text-[16px]  text-OnButtonNormal ${item.value == dataClick ? "text-white" : "ont-semibold"}`}  >{item.label}</div>
                                </div>
                            )
                        })
                    }

                </div>
                <div className='mt-[77.5px] font-cblock text-[20px] font-semibold'>Events</div>
                <div className='mt-[27.5px]'>
                    {
                        cards.length > 0 ? cards.map(e => {
                            return (
                                <div style={{ boxShadow: "0px 0px 100px -10px rgba(0, 0, 0, 0.15)" }}
                                    className='mb-[10px] bg-white flex items-center   w-[464px] h-[120px] rounded-[20px]'  >
                                    <div className=' pl-[56px] font-cblock font-semibold text-[16px]'>{e?.time}</div>
                                    <div className='ml-[31px]'>
                                        <div className=' font-cblock font-semibold text-[16px]'>{e.eventName}</div>
                                        <div className=' font-cblock font-medium text-[14px]'>Room {e?.elderCommonVO[0]?.roomNumber}</div>
                                        <div className=' font-cblock font-medium text-[14px]'>{e.firstName} {e.lastName} {e.age ? (e.age) : ""}</div>
                                    </div>
                                    <div className='ml-[104px]'>  <img src={e.imgUrl} /></div>
                                </div>
                            )
                        }) :
                            (
                           
                                  <NoEvent text="No Events Available" />
                               
                            )
                    }
                </div>
            </div>
        </>
    )
}