import React, { useEffect, useRef, useState } from "react";
import { selfList } from "@/api/RecentAlerts";
const RoomView = ({ activeTab, elderLists }) => {
  const [data, setData] = useState([]);
  console.log(elderLists);

  // const getlist = () => {
  //     selfList()
  //         .then(data => {

  //             setData(data.data)
  //         })
  // }
  useEffect(() => {
    // getlist()
    console.log(elderLists);
  }, [activeTab]);
  return (
    <div className="flex flex-wrap">
      {elderLists.map((e, ri) => {
        return (
          <div
            key={ri}
            className="min-w-[50px] h-[60px] flex items-center justify-center mb-[20px] mr-[20px] "
          >
            <div
              style={{
                border: "1px solid #E2E3E5",
                background:
                  e.priority === 2
                    ? "#eb3d3d"
                    : e.priority === 1
                    ? "#FAB515"
                    : "#fff",
              }}
              className={`${
                e.priority > 0 ? "text-[#fff] div-animation " : ""
              } px-[3px] rounded-[10px] min-w-[50px] h-[50px] flex items-center justify-center  bg-lightwhite `}
            >
              {e.roomNumber && e.roomNumber.length > 4 ? (
                <small>{e.roomNumber}</small>
              ) : (
                e.roomNumber || "* *"
              )}
            </div>
          </div>
        );
      })}

      {elderLists?.length === 0 ? (
        <div
          className="flex pt-[20px] pb-[20px] items-center relative"
          style={{ width: "100%", height: "200px", color: "#bbb" }}
        >
          <div style={{ width: "100%", textAlign: "center" }}>All Good</div>
        </div>
      ) : null}
    </div>
  );
};
export default RoomView;
